/* General Reset */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: #f8f8f8;
}

/* Navbar Styling */
.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(to right, #333, #555);
  padding: 15px 20px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  position: fixed;
}

/* Nav Link Styling */
.nav-link {
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 12px 20px;
  margin: 0 10px;
  border-radius: 5px;
  transition: 0.3s ease-in-out;
  font-weight: bold;
}

/* Hover Effect */
.nav-link:hover {
  background: #eea1c8;
  color: #333;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transform: scale(1.05);
}


@media (max-width: 768px) {
  .navbar {
      flex-direction: column;
      padding: 10px;
  }
  .nav-link {
      display: block;
      width: 100%;
      text-align: center;
      margin: 5px 0;
  }
}

 
