.tech-stack-container {
    text-align: center;
    padding: 40px;
    background: linear-gradient(135deg, #1e1e2f 0%, #28293e 100%);
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  }
  
  .title {
    font-size: 2rem;
    color: #fff;
    margin-bottom: 20px;
  }
  
  .tech-icons {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .tech-item {
    background: rgba(255, 255, 255, 0.1);
    padding: 20px;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
    text-align: center;
  }
  
  .tech-item:hover {
    transform: scale(1.1);
    background: rgba(255, 255, 255, 0.2);
  }
  
  .img {
    width: 80px;
    height: 80px;
    margin-bottom: 10px;
  }
  
  span {
    display: block;
    color: #fff;
    font-size: 1.2rem;
    font-weight: bold;
  }
  